/* HoneycombAnimation.css */
.honeycomb-animation {
  position: absolute; /* Make position absolute to appear relative to button */
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: flex;
  justify-content: center;
  align-items: center;
  pointer-events: none;
  z-index: 1000;
}

.honeycomb-cell {
  width: 60px;
  height: 60px;
  background-color: #ffcc00;
  clip-path: polygon(
    50% 0%,
    100% 25%,
    100% 75%,
    50% 100%,
    0% 75%,
    0% 25%
  );
  opacity: 0;
  position: absolute;
}

.honeycomb-cell-1 {
  width: 60px;
  height: 60px;
  background-color: #ffcc00; /* Fallback color */
  /* background-image: url('public/images/Swarm_Intelligence.png');  */
  background-size: cover; 
  background-position: center; 
  clip-path: polygon(
    50% 0%,
    100% 25%,
    100% 75%,
    50% 100%,
    0% 75%,
    0% 25%
  );
  opacity: 0;
  position: absolute;
}

