@keyframes appear-and-move-1 {
  0% {
    opacity: 0;
    transform: translate(0, 0) scale(0.5);
  }
  100% {
    opacity: 1;
    transform: translate(-80px, -150px) scale(1); /* Adjusted spacing */
  }
}

@keyframes appear-and-move-2 {
  0% {
    opacity: 0;
    transform: translate(0, 0) scale(0.5);
  }
  100% {
    opacity: 1;
    transform: translate(0px, -180px) scale(1); /* Adjusted spacing */
  }
}

@keyframes appear-and-move-3 {
  0% {
    opacity: 0;
    transform: translate(0, 0) scale(0.5);
  }
  100% {
    opacity: 1;
    transform: translate(80px, -150px) scale(1); /* Adjusted spacing */
  }
}
